/******* Star style ********/

.rating-stars {
    display: inline-block;
    position: relative;
    width: 70px;
    margin-top: -5%;
    padding-top: 0px;
}

.rating-text {
    font-size: 15px;
}

.rating-stars:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgb(214, 208, 208);
}

.star-image {
    height: 100%;
    width: 100%;
    display: block;
    position: relative;
    z-index: 1;
}

.rating-stars .filled-star {
    position: absolute;
    left: 0;
    top: 0;
    width: 91%;
    height: 100%;
    background: #ffc107;
}

.rating-stars Image {
    height: 100%;
    width: 100%;
    display: block;
    position: relative;
    z-index: 1;
}

